body {
  padding: 0;
  margin: 0 0;
  font-family: 'Raleway', sans-serif;
  background-image: url(/Clicky-Game/static/media/forest.f9477409.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.navbar li {

  display: inline-block;
  text-align: center;
}

.itemCenter {

  width: 100%;
  color: white;
  font-size: 5vh;
  font-weight: bold;
}

.navBackground {

  background: rgba(0, 83, 28, 0.95);
  padding-top: .7vw;
  padding-bottom: .7vw;
  border-color: rgba(0, 66, 22, 0.95);
}

.header {

	position: relative;
	width: 100%;
	background: rgba(0, 0, 0, 0.5);
	font-family: 'Raleway', sans-serif;
}

h1 {

	text-align: center;
	font-size: 11vh;
	font-weight: bold; 
	color: white;
	padding-top: 13vh;
}

h2 {

	text-align: center;
	font-size: 3vh;
	padding-bottom: 5vh;
	color: white;
	padding-top: 2vh;
}

.imgDivDiv {

  margin-bottom: 2vh; 
}

.imgDiv {

  width: 30vw;
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
  border-radius: 5px;
}

.image {

  width: 100%;
}
.wrapper {
  
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding-top: 2vh;
  padding-left: 3vw;
  padding-right: 3vw;
  justify-content: space-around;
  align-content: flex-start;
  margin-bottom: 5vh;
}

